import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnChanges,
  Output,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import {FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import { FormConfigTypes } from '../enums';
import { FormularioService } from '../formulario.service';
import { FormConfig, FormFieldConfig } from '../interfaces';

@Component({
  selector: 'app-dyn-form',
  templateUrl: './dyn-form.component.html',
  styleUrls: ['./dyn-form.component.scss'],
  providers: [ FormularioService ],
  encapsulation: ViewEncapsulation.None
})
export class DynFormComponent implements AfterViewInit, OnDestroy, OnChanges {
 
  fieldsP: FormFieldConfig[]  = []
  enum = FormConfigTypes
  valueChanges: Subscription;
  @Input() cols: number = 2;
  @Input() gutterSize: string = '1em';
  @Input() rowHeight: string = '130px'
  @Input() fields!: FormFieldConfig[] 
  @Output() submitEvent: EventEmitter<any>= new EventEmitter()
  @Output() onChangeEvent: EventEmitter<any> = new EventEmitter()

  /**
   * Valores a setear en el formulario
   * 
   * Setea los valores solo a los campos de las propiedades del objeto
   * 
   * Si se quiere limpiar/resetear todo el formulario, pasar null
   */
  @Input() FieldsValues: any;

  @Input() formConfig: FormConfig = {
    formCustomClass: '',
    submitButtonConfig:{
      text:'SUBMIT',
      color:'primary',
      hidden: false,
      materialButtonType:'raised'
    }
  };

  forma: FormGroup;

  constructor(private cdRef: ChangeDetectorRef, private fs: FormularioService) {
    
  }
  ngOnChanges(changes: SimpleChanges): void {
    if( !!changes['FieldsValues'] && changes['FieldsValues'].previousValue != changes['FieldsValues'].currentValue ) {
      this.SetValues()
    }
  }

  get FormValues(){
    return this.forma.value
  }

  ngOnInit(): void {
    this.getRowHeight();
  }

  private SetValues() {
    if (!!this.forma) {
      if (!!this.FieldsValues)
        Object.keys( this.FieldsValues ).forEach( key => {
          this.forma.get(key)?.setValue( this.FieldsValues[key])
        });
      else 
        this.forma.reset();
    }
  }

  ngAfterViewInit() {
    this.forma = this.fs.buildFormGroup(this.fields);
    // console.log(this.forma);
    this.fieldsP = this.fields
    this.valueChanges = this.forma.valueChanges.subscribe( v => {
      this.onChangeEvent.emit( this.forma.value )
    });
    if (!!this.FieldsValues)
      this.SetValues();
  }

  ngAfterViewChecked() {
    this.cdRef.detectChanges();
  }

  ngOnDestroy(): void {
    this.valueChanges.unsubscribe();
    this.fs.ngOnDestroy()
  }

  OnSubmit(){
    // console.log(this.forma)
    this.forma.markAllAsTouched()
    this.submitEvent.emit(this.FormValues)
  }


  getRowHeight() {
    var matchesNumber = this.rowHeight.match(/(\d+)/);
    let matchesString = this.rowHeight.replace(/[0-9]/g, '');
    this.rowHeight = (Number((matchesNumber as any)[0]) / 5 ).toString() + matchesString
    return this.rowHeight;
  }

}
