import {
  Component,
  Input,
  OnDestroy,
  OnInit,
  Optional,
  Self,
  DoCheck,
} from '@angular/core';
import {
  AbstractControl,
  FormBuilder,
  FormGroup,
  NgControl,
} from '@angular/forms';
import { MatDateRangePicker } from '@angular/material/datepicker';
import { Subscription } from 'rxjs';
import { BaseFormFieldComponent } from '../base-form-control/base-form-control';

@Component({
  template: '',
})
export class DateRangePickerControlComponent
  extends BaseFormFieldComponent
  implements OnInit, DoCheck, OnDestroy
{
  @Input() editable: boolean = false;

  @Input() openOnClick = true;

  @Input() placeHolderFromDate: string = 'dd/MM/yyyy';
  @Input() placeHolderToDate: string = 'dd/MM/yyyy';

  rangeForm: FormGroup;
  changesSub: Subscription;

  constructor(
    private fb: FormBuilder,
    @Optional() @Self() public override controlDir: NgControl
  ) {
    super(controlDir);
  }

  ngOnInit(): void {
    this.buildAndLoadForm();
    this.validarRequired();
  }

  buildAndLoadForm() {
    this.rangeForm = this.fb.group({
      start: [],
      end: [],
    });

    this.listenChanges();

    // console.log('valores  input?',this.controlDir.control?.value)
    if (this.controlDir.value) {
      this.rangeForm.patchValue({
        start: this.controlDir.control?.value.desde,
        end: this.controlDir.control?.value.hasta,
      });
    }
  }

  togglePicker(picker: MatDateRangePicker<any>) {
    if (this.openOnClick) {
      picker.open();
    }
  }

  override writeValue(value: any): void {
    if (!!this.rangeForm && value === null) {
      this.rangeForm.reset();
    }
    this.value = value;
  }

  listenChanges() {
    this.changesSub = this.rangeForm.valueChanges.subscribe((values) => {
      this.controlDir.control?.setValue(values);
      //Si es requerido valido que ambos campos (start o end)tengan valor
      this.validarRequired();
    });
  }

  validarRequired() {
    const noDatesInForm = Object.keys(this.rangeForm.value).some(
      (k) => !!!this.rangeForm.value[k]
    );

    const validator =
      !!this.controlDir.control?.validator &&
      this.controlDir.control.validator({} as AbstractControl);

    if (noDatesInForm) {
      if (!!this.controlDir.control?.validator) {
        if (!!validator && !!validator['required']) {
          this.controlDir.control.setErrors({
            ...this.controlDir.control.errors,
            required: validator['required'],
          });
        }
      }
    }
    if (this.controlDir.control) {
      this.isRequired =
        this.controlDir.control.invalid &&
        Boolean(validator && validator.hasOwnProperty('required'));
    }
  }

  override ngDoCheck() {
    if (this.controlDir.control?.touched) {
      this.rangeForm.markAllAsTouched();
      this.validarRequired();
    }
  }

  ngOnDestroy(): void {
    this.changesSub.unsubscribe();
  }
}
