import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-page-size',
  templateUrl: './page-size.component.html',
  styleUrls: ['./page-size.component.scss']
})
export class PageSizeComponent implements OnInit {

  @Input() pageSize: number = 10;
  previusPageSize: number = this.pageSize
  
  @Output() sizeChangeEvent: EventEmitter<number> = new EventEmitter<number>()
  @Input() pageSizeText: string 

  ngOnInit() {
  }

  onSizeChange(){
    if(this.previusPageSize != this.pageSize){
      this.sizeChangeEvent.emit(this.pageSize)
      this.previusPageSize = this.pageSize
    }
  }


}
