import { ErrorsConfigurations } from "./errors-configuration";

export const defaultErrorMessages: ErrorsConfigurations = {
    'required': (label?: string) => 'El campo es requerido',
    'maxlength': (label?: string, error?: any) => `El campo no puede ser mayor a ${error.maxlength}`,
    'minlength': (label?: string, error?: {actualLength: number, requiredLength: number}) => label + ' minLength!! ' + error?.requiredLength,
    'cuit': (label?: string) => 'El campo no tiene el formato correcto',
};