import { Inject, Injectable } from '@angular/core';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import { SweetAlertIcon, SweetAlertInput, SweetAlertPosition } from 'sweetalert2';
import { LinkSweet } from '../models/linkSweet.class';
import { CustomClassSweet } from '../models/customClassSweet.interface';


@Injectable({
  providedIn: 'root'
})
export class AlertService {

  private swalWithMatButtonsBasic!: typeof Swal;
  private swalWithMatButtonsDialog!: typeof Swal;
  private swalWithMatButtonsConfirm!: typeof Swal;

  constructor( @Inject('customClass') customClass?: CustomClassSweet ) { 

    if(customClass){
      this.swalWithMatButtonsBasic = Swal.mixin({
        customClass: {
          confirmButton: customClass.basic
        },
        buttonsStyling: false
      });
  
      this.swalWithMatButtonsDialog = Swal.mixin({
        customClass: {
          confirmButton: customClass.dialog.confirmButton,
          cancelButton: customClass.dialog.cancelButton,
          denyButton: customClass.dialog.denyButton
        },
        buttonsStyling: false
      });
  
      this.swalWithMatButtonsConfirm = Swal.mixin({
        customClass: {
          confirmButton: customClass.confirm.confirmButton,
          cancelButton: customClass.confirm.cancelButton,
          input: 'mat-typography'
        },
        buttonsStyling: false
      });
    }else{
      this.swalWithMatButtonsBasic = Swal.mixin({});
      this.swalWithMatButtonsDialog = Swal.mixin({});
      this.swalWithMatButtonsConfirm = Swal.mixin({});
    }
  }

  
  showBasic( 
    title?: string, 
    text?: string, 
    icon?: SweetAlertIcon 
  ){

    this.swalWithMatButtonsBasic.fire(
      title,
      text,
      icon
    );

  }

  showCustom( 
    position?: SweetAlertPosition, 
    title?: string, 
    text?: string, 
    icon?: SweetAlertIcon, 
    timer: number = 2500, 
    showConfirmButton: boolean = false, 
    confirmButtonText: string = 'OK' 
  ){

    if(timer){
      showConfirmButton = false;
      confirmButtonText = '';
    }

    this.swalWithMatButtonsBasic.fire({
      position,
      title,
      text,
      icon,
      timer,
      showConfirmButton,
      confirmButtonText
    });

  }

  showCustomHTML( 
    position?: SweetAlertPosition, 
    title?: string, 
    html?: string, 
    icon?: SweetAlertIcon, 
    showCloseButton: boolean = true,
    showCancelButton: boolean = true,
    focusConfirm: boolean = false,
    confirmButtonText?: string,
    cancelButtonText?: string
  ){

    if(!showCancelButton) cancelButtonText = '';

    this.swalWithMatButtonsConfirm.fire({
      position,
      title,
      html,
      icon,
      showCloseButton,
      showCancelButton,
      focusConfirm,
      confirmButtonText: confirmButtonText ? confirmButtonText : 'OK',
      cancelButtonText: cancelButtonText ? cancelButtonText : 'CANCELAR',
      reverseButtons: true
    });
    
  }

  showCustomImage( 
    imageUrl: string,
    position?: SweetAlertPosition, 
    title?: string,
    text?: string,
    imageWidth: number = 360,
    imageHeight: number = 160,
    imageAlt: string = 'Custom image',
  ){

    this.swalWithMatButtonsBasic.fire({
      position,
      title,
      text,
      showConfirmButton: false,
      showCloseButton: true,
      imageUrl,
      imageWidth,
      imageHeight,
      imageAlt
    });

  }
 
  async showDialog( 
    title?: string, 
    text?: string,
    confirmButtonText?: string,
    denyButtonText?: string,
    cancelButtonText?: string,
    mode: boolean = true,
  ): Promise<any>{

    return await this.swalWithMatButtonsDialog.fire({
      title,
      text,
      showDenyButton: true,
      showCancelButton: true,
      confirmButtonText: confirmButtonText ? confirmButtonText : 'OK',
      denyButtonText: denyButtonText ? denyButtonText : 'DENEGAR',
      cancelButtonText: cancelButtonText ? cancelButtonText : 'CANCELAR',
      reverseButtons: mode
    });

  }

  async showConfirm( 
    title?: string, 
    text?: string,
    icon?: SweetAlertIcon, 
    confirmButtonText?: string,
    cancelButtonText?: string,
  ){

    return await this.swalWithMatButtonsConfirm.fire({
      title,
      text,
      icon,
      showCancelButton: true,
      confirmButtonText: confirmButtonText ? confirmButtonText : 'OK',
      cancelButtonText: cancelButtonText ? cancelButtonText : 'CANCELAR',
      reverseButtons: true
    });
  
  }

  showBasicErrorFooter( 
    title?: string, 
    text?: string, 
    link: LinkSweet | HTMLElement | string = '', 
    icon?: SweetAlertIcon  
  ){

    const footer: LinkSweet | HTMLElement | string = 
      link instanceof LinkSweet ? `<a href="${link.redirecTo}" target="_blank">${link.text}</a>` : link;

      this.swalWithMatButtonsBasic.fire({
      icon,
      title,
      text,
      footer
    });

  }

  async showConfirmWithInputHtml( 
    title?: string, 
    text?: string,
    icon?: SweetAlertIcon, 
    confirmButtonText?: string,
    cancelButtonText?: string,
    html?: string | HTMLElement | JQuery,
    preConfirm?: (inputValue: any) => any | false | undefined | Promise<any | false | undefined>,
    input?: SweetAlertInput,
    inputLabel?: string,
    inputPlaceholder?: string,
    inputOptions?: ReadonlyMap<string, string> | Record<string, any> | Promise<ReadonlyMap<string, string> | Record<string, any>>,
    inputAttributes?: Record<string, string>,
    inputValidator?: (inputValue: string) => string | null | Promise<string | null>,
    inputValue?: string | number | boolean | Promise<string | number | boolean>,
  ){

    return await this.swalWithMatButtonsConfirm.fire({
      title,
      text,
      icon,
      showCancelButton: true,
      confirmButtonText: confirmButtonText ? confirmButtonText : 'OK',
      cancelButtonText: cancelButtonText ? cancelButtonText : 'CANCELAR',
      reverseButtons: true,
      html: html,
      preConfirm: preConfirm,
      input: input,
      inputLabel: inputLabel,
      inputPlaceholder: inputPlaceholder,
      inputOptions: inputOptions,
      inputAttributes:inputAttributes,
      inputValidator: inputValidator,
      inputValue: inputValue,
    });
  
  }

}
