import { AbstractControl, ValidationErrors, ValidatorFn, Validators } from "@angular/forms"

export class DataValidators {

  static cuitValidator(customMessage?: string): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      const value: string = control.value!! ? control.value.toString() : null;
      if (value) {
          if (!value.match(/^(23)|(27)|(20)|(30)/)) {
              return { cuit: { currentValue: value, customMessage } };
          }
      }
      return null;
    }
  }

  static email(customMessage?: string): ValidatorFn {
    return (control: AbstractControl) => {
      if (!!Validators.email(control))
        return { email: { email: true, customMessage }}; 
      return null;
    }
  }
  
}
