import { Component, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild, EventEmitter } from '@angular/core';
import { TextControlComponent } from '../../base/text-control/text-control.component';
import { ErrorMaterialComponent } from '../error/error.component';
import { TypeDocuments } from './type.enum';

@Component({
  selector: 'sof-doc-control',
  templateUrl: './doc-control.component.html',
  styleUrls: ['./doc-control.component.scss']
})
export class DocControlComponent extends TextControlComponent implements OnInit, OnChanges {
  @ViewChild(ErrorMaterialComponent) errorControl: ErrorMaterialComponent;
  
  @Input() type: TypeDocuments = 'DNI';
  @Output() minLength: EventEmitter<number> = new EventEmitter<number>();
  customMask: any;
  
  ngOnInit(): void {
    this.setMask();
  }
  
  ngOnChanges(changes: SimpleChanges): void {
    if(changes){
      setTimeout(() => { this.value = null }, 0);
      this.setMask();
    }
  }

  setMask(){
    switch (this.type) {
      case 'DNI':
        this.customMask = '0.000.000||00.000.000';
        this.minLength.emit(9);
        break;

      case 'CUIL': 
        this.customMask = '00-0.000.000-0||00-00.000.000-0';
        this.minLength.emit(14);
        break;

      case 'PASAPORTE':
        this.customMask = 'SSS000';
        this.minLength.emit(6);
        break;
      
      default:
        this.customMask = '00.000.000||0.000.000';
        this.minLength.emit(9);
        break;
    }
  }
}
