import { Directive, HostListener, Input } from '@angular/core';

@Directive({
  selector: '[IntegerInput]',
})
export class IntegerInputDirective  {
  
  @Input() ignoreSpaces: boolean = true;
  private allowedKeyCodes: number[] = [
    13, //Enter 
    8,  //Backspace
    46, //Delete
    9,  //Tab
    35, //End
    36, //Home
    37, //Left
    39, //Right
    17, //Ctrl
  ]

  constructor() { }

  private validInputCode(code: any) : boolean {
    return this.allowedKeyCodes.indexOf(code) != -1;
  }

  private isPasteEvent(event: any) : boolean {
    return event.ctrlKey && event.key == "v";
  }

  //Can be deleted since keydown is triggered before
  @HostListener('keypress', ['$event'])
  onKeypress(event: any) {
    const notNumbers = /[^0-9]/;
    if (notNumbers.test(event.key) && !this.validInputCode(event.which ? event.which : event.keyCode) && !this.isPasteEvent(event)) {
      event.preventDefault();
      event.stopImmediatePropagation();
      return false;
    }
    
    return true;
  }

  //Can be deleted since keydown is triggered before
  @HostListener('keyup', ['$event'])
  onKeyup(event: any) {
    const notNumbers = /[^0-9]/;
    if (notNumbers.test(event.key) && !this.validInputCode(event.which ? event.which : event.keyCode) && !this.isPasteEvent(event)) {
      event.preventDefault();
      event.stopImmediatePropagation();
      return false;
    }
    
    return true;
  }

  @HostListener('keydown', ['$event'])
  onKeydown(event: any) {
    const notNumbers = /[^0-9]/;
    if (notNumbers.test(event.key) && !this.validInputCode(event.which ? event.which : event.keyCode) && !this.isPasteEvent(event)) {
      event.preventDefault();
      event.stopImmediatePropagation();
      return false;
    }
    
    return true;
  }

  //Can be deleted since keydown is triggered before
  @HostListener('input', ['$event'])
  onInput(event: any) {
    const notNumbers = /[^0-9]/;
    if (notNumbers.test(event.data)) {
      event.preventDefault();
      event.stopImmediatePropagation();
      return false;
    }
    
    return true;
  }

  //Can be deleted since keydown is triggered before
  @HostListener('ionChange', ['$event'])
  onIonChange(event: any) {
    event.currentTarget.value = event.currentTarget.value.replace(/\D/g,'');
  }

  @HostListener('paste', ['$event'])
  onPaste(event: any) {
    const notNumbers = /[^0-9]/;
    let input = event.clipboardData.getData('text');

    if (this.ignoreSpaces)
      input = input.replaceAll(' ', '');

    if (notNumbers.test(input)) {
      event.preventDefault();
      event.stopImmediatePropagation();
      return false;
    }
    
    return true;
  }

  @HostListener('drop', ['$event'])
  onDrop(event: any) {
    const notNumbers = /[^0-9]/;
    let input = event.dataTransfer.getData("text");

    if (this.ignoreSpaces)
      input = input.replaceAll(' ', '');
    
    if (notNumbers.test(input)) {
      event.preventDefault();
      event.stopImmediatePropagation();
      return false;
    }
    
    return true;
  }

}