import { Injectable } from '@angular/core';
import { GridApi } from 'ag-grid-community';

@Injectable()
export class GridHandlerService {

  private gridApi!: GridApi;
  private rowData: any[] = [];
  private dataId: any = 'id';

  get RowData() {
    return this.rowData;
  }

  get isSelectedRows(){
    return this.gridApi?.getSelectedNodes().length > 0;
  }

  get selectedRows(){
    return this.gridApi?.getSelectedNodes();
  }

  constructor() {}

  setConfigs(gridApiParam: GridApi, rowDataParam: any[], dataIdParam: any) {
    this.gridApi = gridApiParam;
    this.rowData = rowDataParam;
    this.dataId = dataIdParam;
  }

  addRow(objToAdd: any){
    if (this.rowData.length > 0) {
      const indexRepeat = this.rowData.findIndex((row: any) => row[this.dataId] === objToAdd[this.dataId]);
      if (indexRepeat === -1) this.finalTransaction([ ...this.rowData, objToAdd ]);

    } else {
      this.finalTransaction([ ...this.rowData, objToAdd ]);
    }
  }

  updateRowState(obj: any, action: string, column: string = 'Estado'){
    const indexObj = this.rowData.findIndex( (row) => row[this.dataId] === obj[this.dataId] );
    if(indexObj === -1) return;

    this.rowData[indexObj][column] = action;
    this.finalTransaction(this.rowData);
  }

  updateDataState( action: string ){
    let newRowData: any[] = this.rowData.map((row) => { return  { ...row, Estado: action } } );
    this.finalTransaction(newRowData);
  }

  deleteRowsSelected() {
    if(this.selectedRows.length > 0) 
    this.selectedRows.forEach( (node: any) => {
      let newRowData: any[] = this.rowData.filter((row) => row[this.dataId] !== node.id);
      this.finalTransaction(newRowData);
    });
  }

  finalTransaction( data: any[] ){
    this.rowData = data;
    this.gridApi.setRowData(this.rowData);
    this.gridApi.redrawRows();
  }
  
}
