/**
 * Todos los disntintos tipos de validacion
 */
export enum ValidationTypes {
    Required = 'required',
    Min = 'min',
    Max = 'max',
    Function = 'function',
    MinLength = 'minLength',
    MaxLength = 'maxLength',
    Pattern = 'pattern',
    MinDate = 'minDate',
    MaxDate = 'maxDate', 
    AfterOrEqualTo = 'afterOrEqualTo',
    BeforeOrEqualTo = 'beforeOrEqualTo',
    MaxFileSize = 'maxFileSize',
    AcceptedTypes = 'AcceptedTypes',
    Email = 'email',
    // Cross Field Validations (requires fieldTarget)
    RequiredIf = 'requiredIf',
    RequiredIfFieldIsEmpty = 'requiredIfFieldIsEmpty',
    RequiredIfFieldIsNotEmpty = 'requiredIfFieldIsNotEmpty',
    LessOrEqualTo = 'lessOrEqualTo',
    MoreOrEqualTo = 'moreOrEqualTo'
  }