export class LinkSweet{
    private _text: string;
    private _redirecTo: string;
  
  
    public get text(): string {
        return this._text;
    }
    public set text(value: string) {
        this._text = value;
    }
   
    public get redirecTo(): string {
        return this._redirecTo;
    }
    public set redirecTo(value: string) {
        this._redirecTo = value;
    }
  
    constructor(text: string, redirecTo: string){
      this._redirecTo = redirecTo;
      this._text = text;
    }
  }
  