import { Injectable } from '@angular/core';
import {
  ActionForm,
  ConditionForm,
  Emptyfield,
  FieldConfig,
  FormFieldConfig,
  ValidationForm,
} from './interfaces';
import { ActionFormTypes, FormConfigTypes, ValidationTypes } from './enums';
import { Subscription, Observable, combineLatest, map } from 'rxjs';
import {
  AbstractControl,
  FormBuilder,
  FormControl,
  FormGroup,
} from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { DateTime } from 'luxon';
import { PrefixConfig } from './interfaces/prefix-config';
import {
  DataValidators,
  RequiredValidators,
  NumberValidators,
  TextValidators,
  FileValidators,
  DateTimeValidators,
} from '../validations';

@Injectable({
  providedIn: 'root',
})
export class FormularioService {
  private subs: Subscription;

  constructor(private fb: FormBuilder, private http: HttpClient) {
    this.subs = new Subscription();
  }

  buildFormGroup(fields: FormFieldConfig[]): FormGroup {
    const formGroup = this.fb.group({});

    fields.forEach((field) => {
      if (field.type == FormConfigTypes.EmptyCol && (field as any)['key']) {
        console.error('Ver FielsConfig en: ', (field as any)['key']);
        throw new Error(
          'No se puede poner type:EmptyCol a un control que tiene que formar parte del formulario'
        );
      }

      if (field.type != FormConfigTypes.EmptyCol) {
        formGroup.addControl(
          field.key,
          new FormControl({
            value: field.defaultValue || '',
            disabled: field.dissabled,
          })
        );
      }
    });

    fields.forEach((field) => {
      if (field.type == FormConfigTypes.Prefix) {
        this.procesarPrefix(formGroup, field);
      }
      if (field.type != FormConfigTypes.EmptyCol) {
        field = field as FieldConfig;
        this.realizarRequestToUrl(formGroup, field);
        const sub = this.procesarAcciones(
          fields.filter(
            (f) => f.type != FormConfigTypes.EmptyCol
          ) as FieldConfig[],
          field,
          formGroup
        );
        if (sub) {
          this.subs.add(sub);
        }

        this.setValidations(
          formGroup.get(field.key) as AbstractControl,
          field.validations,
          formGroup
        );
      }
    });

    return formGroup;
  }

  procesarAcciones(fields: FieldConfig[], field: FieldConfig, form: FormGroup) {
    if (field.action.length == 0) return null;

    return form.get(field.key)?.valueChanges.subscribe((newValue) => {
      field.action.forEach((action: any) => {
        const fieldTarget = this.getFieldTargetFromFields(
          action.fieldTarget,
          fields
        );
        const formControlTarget = form.get(action.fieldTarget) as FormControl;
        if (!formControlTarget)
          throw new Error('No se encontro el formControl');

        switch (action.type) {
          case ActionFormTypes.Hide:
            this.procesarHide(newValue, fieldTarget, action, formControlTarget);
            break;
          case ActionFormTypes.Show:
            this.procesarShow(newValue, fieldTarget, action, formControlTarget);
            break;
          case ActionFormTypes.Reset:
            this.procesarReset(newValue, formControlTarget);
            break;
          case ActionFormTypes.Dissable:
            this.procesarDisable(newValue, formControlTarget, action);
            break;
          case ActionFormTypes.Enable:
            this.procesarEnable(newValue, formControlTarget, action);
            break;
          case ActionFormTypes.SetValue:
            this.procesarSetValue(newValue, formControlTarget, action);
            break;
          case ActionFormTypes.FillOptions:
            this.procesarFillOptions(
              newValue,
              formControlTarget,
              fieldTarget,
              action
            );
            break;
          default:
            throw new Error(
              'No se reconoce el type de la accion: ' + action.type
            );
        }
      });
    });
  }

  procesarPrefix(form: FormGroup, field: FieldConfig) {
    /**
     * Procesel prefix (esucho cambios en dependencies para armar elprefijo dinamico) cuando:
     *   el campo tiene prefixConfig
     *   prefixConfig tiene laconfiguracion dinamica
     *   La configuracion dinamica tiene controles
     */
    if (
      !field.prefixConfig ||
      !field.prefixConfig?.prefixDinamic ||
      field.prefixConfig?.prefixDinamic.controlKeyDependencies.length == 0
    ) {
      return;
    }
    const controlDependencies =
      field.prefixConfig.prefixDinamic?.controlKeyDependencies.map(
        (control) => {
          if (!!!form.get(control)) {
            throw new Error('[Dinamyx prefix] no existe el control ' + control );
            
          }
          return form.get(control)?.valueChanges;
        }
      ) as Observable<number | string>[];

    const prefixDependencies$ = combineLatest(controlDependencies).pipe(
      map((r) => {
        if (r.every((x) => !!x)) {
          return field.prefixConfig?.prefixDinamic?.reduceFn(
            r.map((x) => x.toString())
          );
        } else return '';
      })
    ) as Observable<string>;

    (field.prefixConfig.prefixDinamic as any).prefix$ = prefixDependencies$;
  }

  realizarRequestToUrl(form: FormGroup, field: FieldConfig) {
    if (!field.options?.url) {
      return;
    }
    // console.log('consulto en: ' + field.key);
    // console.log(field.options.url);

    form.get(field.key)!.disable();
    this.http.get(field.options?.url as string).subscribe((res: any) => {
      field.options!.options = res;
      form.get(field.key)!.enable();
    });
  }

  private getFieldTargetFromFields(fieldTarget: string, fields: FieldConfig[]) {
    const ft = fields.find((x) => x.key == fieldTarget) as FieldConfig;
    if (!ft) throw new Error('No se enctontro el field');
    return ft;
  }

  private procesarFillOptions(
    newValue: any,
    formControlTarget: FormControl,
    fieldTarget: FieldConfig,
    action: ActionForm
  ) {
    //Rellenar opciones
    //URL https://apis.datos.gob.ar/georef/api/municipios?provincia=tucuman
    formControlTarget.reset('');
    if (action.fillConfig?.url && newValue) {
      formControlTarget.disable();

      this.http
        .get(`${action.fillConfig!.url}${newValue}`)
        .subscribe((res: any) => {
          let val = res;
          const pathArray = action.dataPath!.split('.');
          for (const prop of pathArray) val = val[prop];

          fieldTarget.options!.options = val;

          formControlTarget.enable();
        });
    } else if (!!action.fillConfig?.fillFn) {
      const options = action.fillConfig?.fillFn(newValue);
      if (options instanceof Promise)
        options.then((x) => (fieldTarget.options!.options = x));
      else fieldTarget.options!.options = options;
    } else {
      fieldTarget.options = action.fillConfig?.fillOptionsConfig?.find(
        (x) => x.onValue == newValue
      )?.optionsConfig;
    }
  }

  private procesarHide(
    newValue: any,
    fieldTarget: FieldConfig,
    action: ActionForm,
    formControlTarget: FormControl
  ) {
    formControlTarget.reset('');

    fieldTarget.hidden = newValue == action.value;
  }

  private procesarShow(
    newValue: any,
    fieldTarget: FieldConfig,
    action: ActionForm,
    formControlTarget: FormControl
  ) {
    formControlTarget.reset('');

    fieldTarget.hidden = !(newValue == action.value);
  }

  private procesarReset(newValue: any, formControlTarget: FormControl) {
    if (formControlTarget?.value != '' && newValue !== '') {
      formControlTarget.reset('');
    }
  }

  private procesarDisable(
    newValue: any,
    formControlTarget: FormControl,
    action: ActionForm
  ) {
    if (newValue == action.value) {
      formControlTarget.disable();
    } else formControlTarget.enable();
  }

  private procesarEnable(
    newValue: any,
    formControlTarget: FormControl,
    action: ActionForm
  ) {
    if (newValue == action.value) {
      formControlTarget.enable();
    } else formControlTarget.disable();
  }

  private procesarSetValue(
    newValue: any,
    formControlTarget: FormControl,
    action: ActionForm
  ) {
    if (!action.newValue)
      throw new Error('La accion debe tener un nuevo valor para setear');

    if (newValue == action.value) {
      formControlTarget.setValue(action.newValue);
    }
  }

  ngOnDestroy(): void {
    this.subs.unsubscribe();
  }

  private setValidations(
    control: AbstractControl,
    validations: ValidationForm[],
    f: FormGroup
  ) {
    for (let validation of validations) {
      switch (validation.type) {
        // Single Fields Validations
        case ValidationTypes.Required:
          this.setRequiredValidation(control, validation.customMessage);
          break;
        case ValidationTypes.Function:
          this.setFuncValidation(
            control,
            validation.func,
            validation.customMessage
          );
          break;
        case ValidationTypes.Min:
          this.setMinValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.Max:
          this.setMaxValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.MinLength:
          this.setMinLengthValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.MaxLength:
          this.setMaxLengthValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.Pattern:
          this.setPatternValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.MinDate:
          this.setMinDateValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.MaxDate:
          this.setMaxDateValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.MaxFileSize:
          this.setMaxFileSizeValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.AcceptedTypes:
          this.setAcceptedTypesValidation(
            control,
            validation.value,
            validation.customMessage
          );
          break;
        case ValidationTypes.Email:
          this.setEmailValidation(control, validation.customMessage);
          break;
        // Cross Fields Validations
        case ValidationTypes.RequiredIf:
          this.setRequiredIfValidation(
            control,
            validation.conditions,
            f,
            validation.customMessage
          );
          break;
        case ValidationTypes.RequiredIfFieldIsEmpty:
          this.setRequiredIfFieldIsEmptyValidation(
            control,
            validation.fieldTarget!,
            f,
            validation.customMessage
          );
          break;
        case ValidationTypes.RequiredIfFieldIsNotEmpty:
          this.setRequiredIfFieldIsNotEmptyValidation(
            control,
            validation.fieldTarget!,
            f,
            validation.customMessage
          );
          break;
        case ValidationTypes.LessOrEqualTo:
          this.setLessOrEqualToValidation(
            control,
            validation.fieldTarget!,
            f,
            validation.customMessage
          );
          break;
        case ValidationTypes.MoreOrEqualTo:
          this.setMoreOrEqualToValidation(
            control,
            validation.fieldTarget!,
            f,
            validation.customMessage
          );
          break;
        case ValidationTypes.AfterOrEqualTo:
          this.setAfterOrEqualToValidation(
            control,
            validation.fieldTarget!,
            f,
            validation.customMessage
          );
          break;
        case ValidationTypes.BeforeOrEqualTo:
          this.setBeforeOrEqualToValidation(
            control,
            validation.fieldTarget!,
            f,
            validation.customMessage
          );
          break;
      }
    }
  }
  // Single Fields Validations

  private setRequiredValidation(
    control: AbstractControl,
    customMessage?: string
  ) {
    control.addValidators(RequiredValidators.required(customMessage));
  }

  private setMinValidation(
    control: AbstractControl,
    minValue: number | undefined,
    customMessage?: string
  ) {
    if (!minValue) {
      throw new Error('minValue must be declared');
    }
    control.addValidators(NumberValidators.min(minValue, customMessage));
  }

  private setMaxValidation(
    control: AbstractControl,
    maxValue: number | undefined,
    customMessage?: string
  ) {
    if (!maxValue) {
      throw new Error('maxValue must be declared');
    }
    control.addValidators(NumberValidators.max(maxValue, customMessage));
  }

  private setMinLengthValidation(
    control: AbstractControl,
    minLength: number | undefined,
    customMessage?: string
  ) {
    if (!minLength) {
      throw new Error('minLength must be declared');
    }
    control.addValidators(TextValidators.minLength(minLength, customMessage));
  }

  private setMaxLengthValidation(
    control: AbstractControl,
    maxLength: number | undefined,
    customMessage?: string
  ) {
    if (!maxLength) {
      throw new Error('maxLength must be declared');
    }
    control.addValidators(TextValidators.maxLength(maxLength, customMessage));
  }

  private setPatternValidation(
    control: AbstractControl,
    pattern: string | RegExp | undefined,
    customMessage?: string
  ) {
    if (!pattern) {
      throw new Error('pattern must be declared');
    }
    control.addValidators(TextValidators.pattern(pattern, customMessage));
  }

  private setMinDateValidation(
    control: AbstractControl,
    minDate: DateTime,
    customMessage?: string
  ) {
    if (!minDate) {
      throw new Error('minDate must be declared');
    }
    control.addValidators(DateTimeValidators.minDate(minDate, customMessage));
  }

  private setMaxDateValidation(
    control: AbstractControl,
    maxDate: DateTime,
    customMessage?: string
  ) {
    if (!maxDate) {
      throw new Error('maxDate must be declared');
    }
    control.addValidators(DateTimeValidators.minDate(maxDate, customMessage));
  }

  private setMaxFileSizeValidation(
    control: AbstractControl,
    maxSize: number,
    customMessage?: string
  ) {
    if (!maxSize) {
      throw new Error('maxSize must be declared');
    }
    control.addValidators(FileValidators.maxFileSize(maxSize, customMessage));
  }

  private setAcceptedTypesValidation(
    control: AbstractControl,
    acceptedTypes: string[],
    customMessage?: string
  ) {
    if (!acceptedTypes) {
      throw new Error('acceptedTypes must be declared');
    }
    control.addValidators(
      FileValidators.acceptedFileTypes(acceptedTypes, customMessage)
    );
  }

  private setEmailValidation(control: AbstractControl, customMessage?: string) {
    control.addValidators(DataValidators.email(customMessage));
  }

  // Cross Fields Validations

  private setFuncValidation(
    control: AbstractControl,
    func: Function | undefined,
    customMessage?: string
  ) {
    if (!func) {
      throw new Error('func must be declared');
    }
    const setError = (error: string) => {
      control.setErrors(
        control.errors
          ? { ...control.errors, [error]: { [error]: true, customMessage } }
          : { [error]: { [error]: true, customMessage } }
      );
    };

    control.valueChanges.subscribe((val) => {
      func(val, setError);
    });
  }

  private setRequiredIfValidation(
    control: AbstractControl,
    conditions: ConditionForm[] | undefined,
    f: FormGroup,
    customMessage?: string
  ) {
    if (!conditions) {
      throw new Error('conditions must be declared');
    }

    for (let condition of conditions) {
      if (!f.get(condition.field)) {
        throw new Error('field not found');
      }

      f.get(condition.field)?.valueChanges.subscribe((val) => {
        if (val == condition.value) {
          control.addValidators(RequiredValidators.required(customMessage));
        } else {
          control.removeValidators(RequiredValidators.required(customMessage));
        }
      });
    }
  }

  private setRequiredIfFieldIsEmptyValidation(
    control: AbstractControl,
    fieldTarget: string,
    f: FormGroup,
    customMessage?: string
  ) {
    this.checkTargetAndSubscribeToRefreshValidations(control, fieldTarget, f);
    control.addValidators(
      RequiredValidators.requiredIfFieldIsEmpty(fieldTarget, customMessage)
    );
  }

  private setRequiredIfFieldIsNotEmptyValidation(
    control: AbstractControl,
    fieldTarget: string,
    f: FormGroup,
    customMessage?: string
  ) {
    this.checkTargetAndSubscribeToRefreshValidations(control, fieldTarget, f);
    control.addValidators(
      RequiredValidators.requiredIfFieldIsNotEmpty(fieldTarget, customMessage)
    );
  }

  private setLessOrEqualToValidation(
    control: AbstractControl,
    fieldTarget: string,
    f: FormGroup,
    customMessage?: string
  ) {
    this.checkTargetAndSubscribeToRefreshValidations(control, fieldTarget, f);
    control.addValidators(
      NumberValidators.lessOrEqualTo(fieldTarget, customMessage)
    );
  }

  private setMoreOrEqualToValidation(
    control: AbstractControl,
    fieldTarget: string,
    f: FormGroup,
    customMessage?: string
  ) {
    this.checkTargetAndSubscribeToRefreshValidations(control, fieldTarget, f);
    control.addValidators(
      NumberValidators.moreOrEqualTo(fieldTarget, customMessage)
    );
  }

  private setAfterOrEqualToValidation(
    control: AbstractControl,
    fieldTarget: string,
    f: FormGroup,
    customMessage?: string
  ) {
    this.checkTargetAndSubscribeToRefreshValidations(control, fieldTarget, f);
    control.addValidators(
      DateTimeValidators.afterOrEqualTo(fieldTarget, customMessage)
    );
  }

  private setBeforeOrEqualToValidation(
    control: AbstractControl,
    fieldTarget: string,
    f: FormGroup,
    customMessage?: string
  ) {
    this.checkTargetAndSubscribeToRefreshValidations(control, fieldTarget, f);
    control.addValidators(
      DateTimeValidators.beforeOrEqualTo(fieldTarget, customMessage)
    );
  }

  private checkTargetAndSubscribeToRefreshValidations(
    control: AbstractControl,
    fieldTarget: string,
    f: FormGroup
  ) {
    if (!fieldTarget) throw new Error('formControlTarget must be declared');

    const formControlTarget = f.get(fieldTarget) as FormControl;
    if (!formControlTarget)
      throw new Error(`${fieldTarget} formControl not exists`);

    this.subs.add(
      formControlTarget.valueChanges.subscribe((val) =>
        control.updateValueAndValidity({ emitEvent: false })
      )
    );
  }
}
