import { Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';
import { Badge } from '../../interfaces/badge-config';

@Component({
  selector: 'app-budge',
  templateUrl: './badge.component.html',
  styleUrls: ['./badge.component.scss']
})
export class BadgeComponent implements ICellRendererAngularComp {

  config: Badge
  params: any

  refresh(params: ICellRendererParams): boolean {
    return true  
  }

  // gets called once before the renderer is used
  agInit(params: ICellRendererParams): void {
    this.params = params
    const configs:  Badge[]  = this.params.badgeConfig;
    const conf = configs.find
      (x => {
        const match =x.condition.values.includes(params.data[x.condition.compareToParam]) 
        return match
      })
    if (conf) 
      {this.config = conf} 
    else 
      {throw Error('No hubo match para encontrar el badge')}
  }


}
