import { Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';
import { MenuOptionConfig } from '../../interfaces';

@Component({
  selector: 'app-row-options',
  templateUrl:'./row-options.component.html',
  styleUrls:['./row-options.component.scss']
})
export class RowOptionsComponent<T> implements ICellRendererAngularComp {
 
  public cellValue!: string;

  public params: any
  options: MenuOptionConfig<T>[]= []
  constructor(){}

  refresh(params: ICellRendererParams): boolean {
    this.params = params;
    return true  
  }

  // gets called once before the renderer is used
  agInit(parameters: ICellRendererParams): void {
    this.params = parameters;
    this.options= this.params.menuOptions.filter((opt: MenuOptionConfig<T>)=> {
      // En el caso de que tenga una condicion, la valido
      if(opt.condicion){
        // Valido que entre los values de la condicion este el valor del parametro que apunto (compareToParam)
        return ( opt.condicion.values.includes( this.params.data[opt.condicion.compareToParam])) ?  opt : null
      }
      // Caso contrario devuelvo la opcion,util para casos en lo que la opcion este presente siempre
      else{
        return opt
      }
    })
  }

  accionar(opt: MenuOptionConfig<T>){
    if (!!opt.actionFn)
      opt.actionFn(this.params.data as T);
  }

}

