import { Component, ElementRef, EventEmitter, Input, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { ErrorMaterialComponent } from '../error/error.component';

@Component({
  selector: 'sof-search-box-control',
  templateUrl: './search-box-control-component.html',
  styleUrls: ['./search-box-control-component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SearchBoxMaterialControlComponent {
  @Input() placeholder!: string;
  @Input() disabled!: boolean;
  @Output() onInput = new EventEmitter<string>();
  @ViewChild(ErrorMaterialComponent) errorControl: ErrorMaterialComponent
  @ViewChild('input') input: ElementRef
  
  onInputEvent(val: string) {
    this.onInput.emit(val);
  }

  reset(emit: boolean = false) {
    this.input.nativeElement.value = '';
    if (emit)
      this.onInput.emit(this.input.nativeElement.value);
  }

}
