import {
  Component,
  Input,
  OnInit,
  ViewChild,
  AfterViewInit,
  ChangeDetectorRef,
  SimpleChanges,
  Output,
  EventEmitter,
} from '@angular/core';

import { AgGridAngular } from 'ag-grid-angular';
import { ColDef, ColGroupDef, GetRowIdFunc, GetRowIdParams, GridApi, RowNode } from 'ag-grid-community';

//Idiomas
import { AG_GRID_LOCALE_ES } from './lang/locale.es'
import { AG_GRID_LOCALE_EN } from './lang/locale.en'
import { OnChanges } from '@angular/core';
import { PageEvent } from '@angular/material/paginator';
@Component({
  selector: 'sof-grid-control',
  templateUrl: './grid-control.component.html',
  styleUrls: ['./grid-control.component.scss']
})
export class GridControlComponent implements OnInit,OnChanges {
  private gridApi!: GridApi;

  @Output() gridReady = new EventEmitter<GridApi>()
  @Output() pageChangeEvent: EventEmitter<PageEvent> = new EventEmitter<PageEvent>() 

  @Input() totalItems: number;
  pageSize: number = 10;

  @Input() onMemorySearch: boolean = true;
  @Input() gridRowOptions: boolean = false
  @Input() gridRowData: any[] | null;
  @Input() gridColDefsRaw: (ColDef | ColGroupDef |string)[] = [];
  @Input() gridPaginationPageSize: number = 10;
  @Input() gridPagination: boolean = true;
  /**
   * @summary gridRowSelection indica el tipo de seleccion en la grilla
   * @param single solo se puede seleccionar una fila a la vez
   * @param multiple se pueden seleccionar varias filas
   */
  @Input() gridRowSelection: 'single' | 'multiple' | 'none' = 'single';
  gridColDefs: (ColDef | ColGroupDef)[];
  @Input() localeText:{[key:string]:string} = AG_GRID_LOCALE_EN
  @Input() gridAnimateRows: boolean= false
  @Input() gridPageSizeEditable: boolean = true
  @Input() gridPageSizeText: string = "Filas por pagina"
  @ViewChild(AgGridAngular) grid: AgGridAngular;

  @Input() gridChecboxesToSelection: boolean = true
  rerender: boolean;

  @Input() rowId: string = 'id';

  /**
   * @summary funcion que devuelve si muestra u oculta el checkbox de seleccion la cada fila
   * @param rowNode la fila de la grilla. rowNode.data es el item de la fila del array que se le pasa a la grilla
   * @example [isRoqSelectableFn]="selectableFn" //Si es necesario '...="selectableFn.bind(this)"'
   * @example selectableFn(rowNode: RowNode): boolean { return rowNode.data?.convId == 'J10'; }
   */
  @Input() isRowSelectableFn?: (rowNode: RowNode) => boolean;


  constructor(
    private _cdRef: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.transformColumnRaws();
  }

  ngOnChanges(changes: SimpleChanges): void {

    /**
     * Espero a que cambie el input de locale para cambiar la propiedad 
     * localeText y volver a renderizar ag-grid con este nuevo locale
     */
    const currentLocale = changes['localeText']?.currentValue
    if(currentLocale && currentLocale != changes['localeText'].previousValue){
      this.changeLocale(currentLocale)
    }
    
  }

  /**
   * Cambia el localeText en la grilla
   * @param locale objeto con las traducciones que necesito en la configuracion de la grilla
   * @example  
   * ```ts
   * { 
     * of: 'de',
      page: 'Pagina',
      nextPage: 'Siguiente',
      lastPage: 'Anterior',
      firstPage: 'Primera pagina',
      previousPage: 'Pagina previa',
    }
   * ```
   */

  changeLocale(locale?:any){
    if(locale){
      this.localeText = locale
    }
    else {
      this.localeText = (this.localeText == AG_GRID_LOCALE_EN) 
      ? AG_GRID_LOCALE_ES : AG_GRID_LOCALE_EN
    }
    this.doRerender()
  }

  /**
   * Metodo para volver a renderizar la grilla con un ngif en el componente de ag-grid
   */
  doRerender(){
    this.rerender = true;
    this._cdRef.detectChanges();
    this.rerender = false;
  }
  

  transformColumnRaws() {
    const columnDefs: ColDef[] = this.gridColDefsRaw.map(
      (x: (string|ColDef), i: number) => {
        //Agrego checkbox selection en primer columna
        if (i == 0 && this.gridChecboxesToSelection) {
          const firstColDef = typeof(x) == 'string' ? 
              this.normaliseStringToColDef(x) : this.normaliseColDef(x)
          firstColDef.headerCheckboxSelection= true
          firstColDef.checkboxSelection= true
          
          return firstColDef;
        }
         else {
            return typeof(x) == 'string' ?
              this.normaliseStringToColDef(x) : this.normaliseColDef(x)
        }
      }
    );

    this.gridColDefs = columnDefs;
  }

  /**
   * Si flex y width estan definidos, flex tiene mas prioridad e ignora el width
   * 
   * De ambos, solo el width se normaliza (200)
   * @summary la Normalizacion de una columna consiste en dejar parametros por default(width, soertable, flex,...)
   */
  normaliseColDef(param: ColDef){
    const cd: ColDef = param;
    cd.suppressMovable = true;
    cd.width = param.width ?? 200;
    cd.minWidth = param.minWidth ?? 150;
    cd.sortable = true;
    cd.resizable = param.resizable ?? false ;
    cd.flex = param.flex;
    return cd;
  }

  normaliseStringToColDef(param: string):ColDef{
    const cd: ColDef = {field:param}
    this.normaliseColDef(cd) 
    return cd;
  }

  onGridReady() {
    this.gridApi = this.grid.api;
    this.gridReady.emit(this.gridApi);
  }

  public getRowId: GetRowIdFunc = (params: GetRowIdParams) => {
    return params.data[this.rowId]; 
  };

  sizeChanged(size: number){
    this.pageSize = size
    this.grid.api.paginationSetPageSize(size);
  }
  
}
