import { Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';
import { DateTime } from 'luxon';
import { DateTimeCellFormat, MenuOptionConfig } from '../../interfaces';

@Component({
  selector: 'app-luxon-datetime-renderer',
  templateUrl:'./luxon-datetime-renderer.component.html',
  styleUrls:['./luxon-datetime-renderer.component.scss']
})
export class LuxonDateTimeRendererComponent implements ICellRendererAngularComp {
 
  public cellValue!: string;
  public params: any;

  constructor(){}

  refresh(params: ICellRendererParams): boolean {
    this.params = params;
    this.setCellValue(this.params);
    return true  
  }

  // gets called once before the renderer is used
  agInit(parameters: ICellRendererParams): void {
    this.params = parameters;
    this.setCellValue(this.params);
  }

  setCellValue(params: any) {
    const config = params.dateTimeConfig as DateTimeCellFormat;

    if (!params.value) 
      this.cellValue = '';
    else if (!!config?.toFormat)
      this.cellValue = (params.value as DateTime).toFormat(config.toFormat, config.opts);
    else if (!!config?.toLocale)
      this.cellValue = (params.value as DateTime).toLocaleString(config.toLocale, config.opts);
    else
      this.cellValue = (params.value as DateTime).toLocaleString(DateTime.DATETIME_SHORT, config?.opts);
  }

}

