import { Component, DoCheck, EventEmitter, Input, Optional, Output, Self, ViewChild } from '@angular/core';
import { ControlValueAccessor, FormControl, NgControl, ValidationErrors } from '@angular/forms';

@Component({
  template: ''
})
export class BaseFormFieldComponent implements ControlValueAccessor, DoCheck {
  @Input() info!: string;
  @Input() description!: string;
  @Input() label!: string;
  @Input() placeholder!: string;
  @Input() maxLength!: string;
  // tslint:disable-next-line: no-output-native
  @Output() change = new EventEmitter<any>();
  @Input() noLabel?: boolean | undefined = false;
  @Input() sideLabel?: boolean | undefined = false;


    value: any;
  disabled = false;
  isRequired = false;
  invalid!: boolean;

  get EsInvalido(){
    return (!!this.controlDir && this.controlDir.touched && this.controlDir.invalid) || false
  }

  onChange = (value: any) => {};
  onTouch = () => {};

  constructor(@Optional() @Self() public controlDir: NgControl) {
    if (controlDir) {
      controlDir.valueAccessor = this;
    }
  }

  ngDoCheck() {
    if (this.controlDir?.control instanceof FormControl) {
      // check if this field is required or not to display a 'required label'
      const validator = this.controlDir.control.errors;
      this.isRequired =
        Boolean(validator && validator.hasOwnProperty('required')) ||
        Boolean(validator && validator.hasOwnProperty('selectedCount'));
    }
  }

  get hasErrors(): boolean {
    return (
      this.controlDir?.control &&
      this.controlDir?.control.touched &&
      !!this.controlDir?.control.errors
    ) ?? false;
  }

  get errors(): ValidationErrors | null {
    return this.controlDir?.control?.errors ?? null;
  }

  writeValue(value: any): void {
    this.value = value;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  setDisabledState(disabled: boolean): void {
    this.disabled = disabled;
  }

  onBlur() {
    this.onTouch();
  }

  protected changeValue(value: any): void {
    this.setInputValue(value);
    this.onTouch();
    this.onChange(this.value);
    this.change.emit(this.value);
  }

  protected setInputValue(value: any): void {
    this.value = value;
  }
}
