import { ModuleWithProviders, NgModule } from '@angular/core';
import { AlertService } from './services/alert.service';
import { SweetAlert2Module } from '@sweetalert2/ngx-sweetalert2';
import { CustomClassSweet } from './models/customClassSweet.interface';

//To implement in style.scss or angular.json
/* @import 'node_modules/sof-ng-controls/assets/alerts/scss/alert.global.scss'; */

@NgModule({
  imports: [
    SweetAlert2Module.forRoot()
  ],
  declarations: [
    
  ],
  providers: [
    AlertService
  ],
  schemas:[]
})
export class AlertModule { 
  static forRoot( customClass: CustomClassSweet | undefined ): ModuleWithProviders<AlertModule> {
    return {
      ngModule: AlertModule,
      providers: [
        AlertModule, 
        { 
          provide: 'customClass', 
          useValue: customClass
        }
      ]
    };
  }
}
