import { Component, Input, ChangeDetectorRef, SimpleChanges, OnChanges } from '@angular/core';
import { ValidationErrors } from '@angular/forms';
import { defaultErrorMessages } from '../../validations/configuration/default-error-messages';
import { ErrorsConfigurations } from '../../validations/configuration/errors-configuration';

@Component({
  selector: 'sof-error',
  template: ''
})
export class ErrorComponent implements OnChanges{

  @Input() translateFuncion: (arg: string) => string = (param:string)=>param;
  @Input() controlInvalido: boolean = false

  @Input() 
  set errors(value: ValidationErrors | null) {
    this.errorsList = value ? Object.keys(value).map(k => this.resolveErrorMessage(k, value[k])) : null;
  }

  get errors(): ValidationErrors | null {
    return this.errorsList as any;
  }

  @Input() label!: string;

  errorsList!: string[] | null;

  constructor(private config: ErrorsConfigurations, private cd: ChangeDetectorRef) {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if(changes['controlInvalido']?.currentValue || !!changes['errors']?.currentValue){
        this.actualizarErrores()
    }
  }
  
  private resolveErrorMessage(errorName: string, value: any): string {

    const msg = !!value.customMessage ? value.customMessage
      : this.config[errorName] ? this.config[errorName](this.label, value) 
      : defaultErrorMessages[errorName] ? defaultErrorMessages[errorName](this.label, value) 
      : '';

    return msg    
  }  

  actualizarErrores(){
    this.errorsList = this.errorsList ? this.errorsList.map(e => this.translateFuncion(e)) : null
  }
}
