import { Component, Input, ViewChild } from '@angular/core';
import { TimePickerControlComponent } from '../../base/time-picker-control/time-picker-control.component';
import { NgxMaterialTimepickerComponent, NgxMaterialTimepickerTheme } from 'ngx-material-timepicker';
import { ErrorMaterialComponent } from '../error/error.component';

@Component({
  selector: 'sof-time-picker-control',
  templateUrl: './time-picker-control.component.html',
  styleUrls: ['./time-picker-control.component.scss'],
})

export class TimePickerMaterialControlComponent extends TimePickerControlComponent {
  // @ViewChild('ngx-material-timepicker')
  // timePicker!: NgxMaterialTimepickerComponent;
  // override onInput(value: any): void {
  //     console.log(value)
  // }

  @ViewChild(ErrorMaterialComponent) errorControl: ErrorMaterialComponent


  themes: NgxMaterialTimepickerTheme = {
    container:{
      bodyBackgroundColor:'#424242',
    }
  }

  @Input() format24hs: boolean = false

  override onInput(value: any): void {
    value = this.normalizeValue(value);
    this.changeValue(value);
  }

  
  normalizeValue(value: string){
    if(!value) return null;
    
    const minutes = Number(value.split(':')[1].substring(0,2))
    
    if(value.includes('AM')){
      // console.log('normaliceAM :>> ', value);
      const hours = Number(value.split(':')[0])
      value = `${hours}:${minutes}` 
    }
    if(value.includes('PM')){
      // console.log('normalicePM :>> ', value);

      let hours = Number(value.split(':')[0]) + 12
      if(hours >= 24) {
        hours -= 24
      }
      value = `${hours}:${minutes}`
    }

    return value
  }

}
